.navbar,
.navbarLogin {
	display: flex;
	flex-direction: column;
	align-self: center;
	position: fixed;
	border-radius: 5px;
	width: 14.3%;
	background-color: white;
	box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.24);
}

.navbarLogin {
	background-color: transparent;
	box-shadow: none;
}
